<template>
  <div v-if="result" class="result card">
    <h3 class="section-title">闯关结果</h3>
    <div class="metrics">
      <div>
        <p class="metrics__value">{{ result.accuracy }}%</p>
        <p class="metrics__label">正确率</p>
      </div>
      <div>
        <p class="metrics__value">{{ result.correct }}/{{ result.total }}</p>
        <p class="metrics__label">正确 / 总数</p>
      </div>
    </div>
    <van-divider />
    <div>
      <p class="section-title">错题回顾</p>
      <p v-if="!result.wrongQuestions.length" class="empty">全部答对，继续保持！</p>
      <template v-else>
        <div
          v-for="item in result.wrongQuestions"
          :key="item.id"
          class="wrong-item"
        >
          <p class="question">{{ item.question }}</p>
          <p class="answer">正确答案：{{ formatAnswer(item) }}</p>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { formatMultipleAnswer } from '@/utils/helper';

export default {
  name: 'ResultDisplay',
  props: {
    result: {
      type: Object,
      default: null,
    },
  },
  methods: {
    formatAnswer(question) {
      return Array.isArray(question.correctAnswer)
        ? formatMultipleAnswer(question.correctAnswer)
        : question.correctAnswer;
    },
  },
};
</script>

<style lang="scss" scoped>
.metrics {
  display: flex;
  justify-content: space-between;
}

.metrics__value {
  font-size: 22px;
  font-weight: 700;
  margin: 0;
}

.metrics__label {
  margin: 4px 0 0;
  color: #6b7280;
  font-size: 12px;
}

.wrong-item {
  padding: 10px 0;
  border-bottom: 1px solid #f3f4f6;
}

.question {
  margin: 0;
  font-weight: 600;
}

.answer {
  margin: 4px 0 0;
  font-size: 12px;
  color: #6b7280;
}

.empty {
  color: #6b7280;
  font-size: 13px;
}
</style>

