<template>
  <div class="result-page">
    <section v-if="!lastResult" class="card empty">
      <van-empty description="暂无闯关记录">
        <van-button type="primary" size="small" @click="$router.push('/quiz')">
          去闯关
        </van-button>
      </van-empty>
    </section>

    <template v-else>
      <result-display :result="resultDetail" />

      <section class="card mt-16">
        <h3 class="section-title">学习建议</h3>
        <p class="suggestion">
          本关正确率 {{ lastResult.accuracy }}%，
          {{ lastResult.accuracy >= 80 ? '可以继续挑战下一关！' : '建议复习错题后再战。' }}
        </p>
        <van-button
          type="primary"
          block
          @click="$router.push('/quiz')"
        >
          继续练习
        </van-button>
        <van-button class="mt-8" plain block @click="$router.push('/')">
          返回首页
        </van-button>
      </section>

      <section v-if="history.length" class="card mt-16">
        <h3 class="section-title">历史记录</h3>
        <van-steps direction="vertical" :active="0">
          <van-step v-for="item in history" :key="item.timestamp">
            <p class="record-title">
              {{ formatLevel(item.levelId) }} · {{ item.accuracy }}%
            </p>
            <p class="record-desc">
              {{ formatTime(item.timestamp) }} · 正确 {{ item.correct }}/{{ item.total }}
            </p>
          </van-step>
        </van-steps>
      </section>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import ResultDisplay from '@/components/result/ResultDisplay.vue';

export default {
  name: 'ResultPage',
  components: { ResultDisplay },
  computed: {
    ...mapGetters('progress', ['lastResult']),
    ...mapState('progress', ['history']),
    ...mapState('questions', ['wrongQuestions']),
    resultDetail() {
      if (!this.lastResult) return null;
      return {
        total: this.lastResult.total,
        correct: this.lastResult.correct,
        accuracy: this.lastResult.accuracy,
        wrongQuestions: this.wrongQuestions,
      };
    },
  },
  methods: {
    formatTime(timestamp) {
      return new Date(timestamp).toLocaleString();
    },
    formatLevel(levelId) {
      if (!levelId) return '未知关卡';
      const parts = levelId.split('-');
      const order = parts[1] || '';
      return `第${order}关`;
    },
  },
};
</script>

<style lang="scss" scoped>
.result-page {
  max-width: 640px;
  margin: 0 auto;
}

.suggestion {
  font-size: 14px;
  color: #374151;
  margin: 0 0 12px;
}

.record-title {
  margin: 0;
  font-weight: 600;
  color: #111827;
}

.record-desc {
  margin: 4px 0 0;
  color: #6b7280;
  font-size: 12px;
}
</style>


